<template>
  <v-form ref="form" lazy-validation v-model="valid">
    <v-row justify="center">
      <v-col xs="12" md="12" lg="6">
        <v-card class="card py-5 my-5 px-7">
          <v-container fluid>
            <center>
              <!-- ==================== -->
              <v-text-field
                label="Name"
                :disabled="disabled"
                outlined
                dense
                v-model="setting.name"
              >
                <v-icon slot="append" color="primary">
                  mdi-form-textbox
                </v-icon></v-text-field
              >
              <!-- ==================== -->

              <v-text-field
                :disabled="disabled"
                label="Email"
                outlined
                dense
                v-model="setting.email"
              >
                <v-icon slot="append" color="primary">
                  mdi-email-send-outline
                </v-icon></v-text-field
              >
              <!-- ==================== -->

              <v-text-field
                :disabled="disabled"
                label="Phone"
                outlined
                dense
                v-model="setting.phone"
              >
                <v-icon slot="append" color="primary">
                  mdi-phone-outline
                </v-icon></v-text-field
              >
              <!-- ==================== -->

              <v-text-field
                :disabled="disabled"
                label="FaceBook "
                outlined
                dense
                v-model="setting.face_book"
              >
                <v-icon slot="append" color="primary">
                  mdi-facebook
                </v-icon></v-text-field
              >
              <!-- ==================== -->

              <v-text-field
                :disabled="disabled"
                label="Twitter"
                outlined
                dense
                v-model="setting.twitter"
              >
                <v-icon slot="append" color="primary">
                  mdi-twitter
                </v-icon></v-text-field
              >
              <!-- ==================== -->

              <v-text-field
                :disabled="disabled"
                label="Youtube"
                outlined
                dense
                v-model="setting.youtube"
              >
                <v-icon slot="append" color="primary">
                  mdi-youtube
                </v-icon></v-text-field
              >
              <!-- ==================== -->
              <v-text-field
                :disabled="disabled"
                label="Whatsapp"
                outlined
                dense
                v-model="setting.youtube"
              >
                <v-icon slot="append" color="primary">
                  mdi-whatsapp
                </v-icon></v-text-field
              >
              <!-- ==================== -->
              <v-text-field
                :disabled="disabled"
                label="Linkedin"
                outlined
                dense
                v-model="setting.linkedin"
              >
                <v-icon slot="append" color="primary">
                  mdi-linkedin
                </v-icon></v-text-field
              >
              <!-- ==================== -->
              <v-text-field
                :disabled="disabled"
                label="Instagram"
                outlined
                dense
                v-model="setting.instagram"
              >
                <v-icon slot="append" color="primary">
                  mdi-instagram
                </v-icon></v-text-field
              >
            </center>
            <!-- build address fields -->
            <v-chip v-text="'Address'" color="primary" outlined> </v-chip>
            <!-- ==================== -->
            <center class="my-5">
              <v-text-field
                :disabled="disabled"
                label="Address Ar "
                outlined
                dense
                v-model="setting.address.ar"
              >
                <v-icon slot="append" color="primary">
                  mdi-map-marker-outline
                </v-icon></v-text-field
              >
              <!-- ==================== -->
              <v-text-field
                :disabled="disabled"
                label="Address En"
                outlined
                dense
                v-model="setting.address.en"
              >
                <v-icon slot="append" color="primary">
                  mdi-map-marker-outline
                </v-icon></v-text-field
              >
              <!-- ==================== -->
              <v-text-field
                :disabled="disabled"
                label="Address Tr"
                outlined
                dense
                v-model="setting.address.tr"
              >
                <v-icon slot="append" color="primary">
                  mdi-map-marker-outline
                </v-icon></v-text-field
              >
              <!-- ==================== -->
            </center>
            <v-btn v-if="!disabled" outlined color="secondary" @click="update">
              <span v-if="!progressUpdate">Update </span>
              <v-progress-circular
                v-if="progressUpdate"
                color="primary"
                indeterminate
              ></v-progress-circular
            ></v-btn>
            <v-btn
              v-if="disabled"
              @click="disabled = false"
              outlined
              color="primary"
            >
              <v-icon>mdi-pencil</v-icon>
              edit
            </v-btn>
          </v-container>
        </v-card>
      </v-col>
    </v-row>
  </v-form>
</template>

<script>
import axios from "axios";
export default {
  mounted() {
    this.getSetting();
  },
  //     if (this.$refs.form.validate()) {}
  data() {
    return {
      disabled: true,
      progressUpdate: false,
      valid: true,
      setting: {
        address: {},
      },
    };
  },
  methods: {
    update: async function () {
      //   var State = false;
      var mainUrl = this.$store.state.mainUrl;
      this.progressUpdate = true;
      await axios
        .request({
          method: "POST",
          url: mainUrl + "/setting/1",
          headers: this.configAxios(),
          data: this.setting,
        })
        .then((res) => {
          // State=true;
          if (res.status == 200) {
            this.ShowAlert("secondary", "Den update Success");
            this.disabled = true;
          }
        })
        .catch((error) => {
          this.ShowAlert("red", "An Error Fail update" + error.response);
        });
      this.progressUpdate = false;
    },
    configAxios() {
      var token = this.$store.state.token;
      return {
        Authorization: "Bearer " + token,
      };
    },
    ShowAlert: function (color, message) {
      this.$store.dispatch("showAlert", {
        color: color,
        message: message,
      });
    },
    getSetting: async function () {
      var mainUrl = this.$store.state.mainUrl;

      await axios.get(mainUrl + "/setting").then((res) => {
        this.setting = res.data.data
          ? res.data.data
          : {
              address: {},
            };
      });
    },
  },
};
</script>

<style>
.card {
  margin: auto !important;
}
</style>